export const contentIsOverset = (contentEl) => {
	if (!contentEl) return false;
	// Firefox sometimes sets scrollHeight to be ~1px larger than
	// clientHeight in cases when `overflow: hidden` is added to
	// an element.
	const TOLERANCE = 2;
	const contentHeight = contentEl.clientHeight + TOLERANCE;
	return contentHeight < contentEl.scrollHeight;
};
