/**
 * TODO-THEMING: We'll eventually need to factor in the space
 * scale of this component.
 *
 * Return the approximate overset height of the alert (with no tolerance)
 * E.g., `75 * (X / 16)`, where `X` is the current page font-size.
 * @returns {number}
 */
export const getComputedOversetHeight = () => {
	/**
	 * 75 is the approximate height of a collapsed, two line alert where
	 * the page's font size is at 1rem (16px).
	 */
	const COLLAPSED_ALERT_HEIGHT = 75;
	const bodyFontSize = parseInt(
		getComputedStyle(document.body).fontSize.slice(0, -2)
	);
	const BASE_FONT_SIZE = 16;
	return COLLAPSED_ALERT_HEIGHT * (bodyFontSize / BASE_FONT_SIZE);
};
