import {createEnhancedElement} from '@servicenow/library-enhanced-element';
import {filterAriaAttributes} from '@servicenow/library-enhanced-utils';
import {Fragment} from '@servicenow/ui-renderer-snabbdom';
import {actionTypes} from '@servicenow/ui-core';
const {COMPONENT_CONNECTED, COMPONENT_PROPERTY_CHANGED} = actionTypes;
import styles from './_now-alert-list.scss';
import './now-alert';
import {t} from 'sn-translate';
import focusBehavior from '@servicenow/behavior-focus';
import {configAriaSchema} from '@servicenow/library-schemas';

const afterNextRender = () => {
	return new Promise((resolve) => window.requestAnimationFrame(resolve));
};

const getAlertDivs = (host) => {
	return [...host.shadowRoot.querySelectorAll('[role="listitem"]')];
};

const getToggleButton = (host) => {
	return host.shadowRoot.querySelector('.now-alert-list-controller-toggle');
};

const alertDismissedHandler = ({host, properties, action, dispatch}) => {
	const {items: sourceItems} = properties;
	const {item} = action.payload;

	// manage focus after the alert is dismissed
	const index = sourceItems.indexOf(item);
	const alertDivs = getAlertDivs(host).reverse(); // reverse alertDivs to match order of sourceItems array
	const previousAlert = alertDivs[index + 1]; // the alerts are displayed in reverse order so the previous one is at the next index of the array
	const toggleButton = getToggleButton(host);
	if (sourceItems.length === 2 && !toggleButton) {
		// TODO-SEISMIC-HACK - we should replace this afterNextRender with proper timing/state management
		// This works when you dismiss the second of two alerts, but not when you dismiss the first of two alerts
		// We might be trying to manage focus too soon. We'll probably need to keep a pointer in state and call `.focus()` in COMPONENT_RENDERED
		afterNextRender().then(() => {
			const nextFocused = getAlertDivs(host)[0]; // normally we would have gotten the nextFocused alertDiv before re-rendering, but Seismic is creating a new alert node when the list is re-rendered instead of reusing the same node, so we can't do that.
			nextFocused.focus();
		});
	} else {
		const nextFocused = previousAlert ? previousAlert : toggleButton;
		if (nextFocused) {
			nextFocused.focus();
		}
	}

	// manage state after the alert is dismissed
	// TODO: remove ternary when Seismic casts properties
	const parsedItems =
		typeof sourceItems === 'string' ? JSON.parse(sourceItems) : sourceItems;
	const items = [...parsedItems];
	items.splice(index, 1);
	dispatch(() => {
		return {
			type: 'NOW_ALERT_LIST#ITEMS_SET',
			payload: {value: items}
		};
	});
};

const ariaLabel = (items, collapsed) => {
	return collapsed
		? t('Collapsed alert list with {0} alerts.', items.length)
		: t('Expanded alert list with {0} alerts.', items.length);
};

const _renderCollapsedAlertList = (items) => {
	const middleAlertStyle = {
		zIndex: '2',
		position: 'relative',
		transform: 'translateY(-90%)',
		outline: 'none' // remove focus outline because this alert is not interactive and only here for visual presentation
	};
	const bottomAlertStyle = {
		zIndex: '1',
		position: 'relative',
		transform: 'translateY(-180%)',
		outline: 'none' // remove focus outline because this alert is not interactive and only here for visual presentation
	};

	const alert1 = (
		<now-alert
			aria-hidden="true"
			tabIndex="-1"
			style={middleAlertStyle}
			status={items[0].status}
			show-shadow
		/>
	);

	const alert2 = items[1] ? (
		<now-alert
			aria-hidden="true"
			tabIndex="-1"
			style={bottomAlertStyle}
			status={items[1].status}
			show-shadow
		/>
	) : (
		undefined
	);

	return (
		<Fragment>
			{alert1}
			{alert2}
		</Fragment>
	);
};

const _renderExpandedAlertList = (items, expandedAlerts, maxHeight) => {
	return (
		<div role="list">
			{items.map((alert) => (
				<div
					className="now-alert-list-alert"
					key={alert.id}
					role="listitem"
					tabIndex="-1"
					aria-label={`Alert level: ${alert.status}`}>
					<now-alert
						append-to-payload={{alertId: alert.id}}
						status={alert.status}
						icon={alert.icon || alert.iconName}
						header={alert.header}
						content={alert.content}
						text-link-props={alert.textLinkProps}
						action={alert.action}
						expanded={expandedAlerts[alert.id]}
						max-height={maxHeight}
						show-shadow
					/>
				</div>
			))}
		</div>
	);
};

const _renderController = (collapsed, onToggleController, onDismissAll) => {
	return (
		<div className="now-alert-list-controller">
			<now-button
				className="now-alert-list-controller-toggle"
				config-aria={{button: {'aria-expanded': collapsed ? 'false' : 'true'}}}
				label={t(collapsed ? 'Show alerts' : 'Hide alerts')}
				on-click={onToggleController}
			/>
			<now-button
				variant="tertiary"
				className="now-alert-list-controller-dismiss"
				label={t('Dismiss All')}
				config-aria={{button: {'aria-label': t('Dismiss all alerts')}}}
				on-click={onDismissAll}
			/>
		</div>
	);
};

const view = (state, {dispatch, updateState}) => {
	const {items, configAria, configAriaLive, maxHeight} = state.properties;
	const {collapsed, userInteractedWithController, expandedAlerts} = state;
	const reversedAlerts = [...items].reverse();

	const onToggleController = () => {
		if (items.length === 1) {
			updateState({userInteractedWithController: false});
		} else {
			updateState(({state}) => ({
				operation: 'merge',
				value: {
					collapsed: !state.collapsed,
					userInteractedWithController: true
				}
			}));
		}
	};

	const onDismissAll = () =>
		dispatch(() => {
			return {
				type: 'PRIVATE#DISMISS_ALL',
				payload: {}
			};
		});

	const controller =
		items &&
		items.length &&
		(collapsed || (!collapsed && userInteractedWithController))
			? _renderController(collapsed, onToggleController, onDismissAll)
			: undefined;

	const alerts =
		items && items.length && collapsed
			? _renderCollapsedAlertList(reversedAlerts)
			: _renderExpandedAlertList(reversedAlerts, expandedAlerts, maxHeight);

	return (
		<div
			className="now-alert-list"
			/* Note: the order of the aria properties here is important for
			 * maintaining backwards compatibility. If `aria-live` is set through
			 * both configAria and configAriaLive, we use the value passed through
			 * configAria. If neither property is set, we use the default value
			 * provided in the deprecated configAriaLive property.
			 */
			aria-label={ariaLabel(items, collapsed)}
			{...filterAriaAttributes(configAria, 'list')}
			aria-live={
				(configAria && configAria.list && configAria.list['aria-live']) ||
				configAriaLive
			}>
			{controller}
			{alerts}
		</div>
	);
};

/**
 * A list of now-alert elements, which can optionally be dismissed.
 *
 * ```jsx
 * <now-alert-list items=[...]></now-alert-list>
 * ```
 * @seismicElement now-alert-list
 * @summary A list of now-alert elements, which can optionally be dismissed.
 */
createEnhancedElement('now-alert-list', {
	properties: {
		/**
		 * List of alerts to display. See `now-alert` component documentation for
		 * valid inputs for all sub-properties. Use `manage-items` to override the
		 * default alert dismissal behavior and handle the `NOW_ALERT_LIST#ITEMS_SET`
		 * action manually.
		 * Assign a globally unique id to each alert to ensure that the alert's
		 * state is correctly maintained as the user interacts with the alert list.
		 * The alert list will not behave as expected if any alerts share the same id.
		 * @type {Alert[]}
		 */
		items: {
			required: true,
			manageable: true,
			schema: {
				type: 'array',
				items: {
					type: 'object',
					properties: {
						id: {type: ['string', 'number']},
						status: {type: 'string'},
						icon: {type: 'string'},
						header: {type: 'string'},
						content: {type: ['string', 'object']},
						textLinkProps: {type: 'object'},
						action: {type: 'object'},
						expanded: {type: 'boolean'},
						showShadow: {type: 'boolean'}
					},
					required: ['id']
				}
			}
		},
		/**
		 * Deprecated, use `config-aria` instead
		 * Optionally configurable assertiveness level for the aria-live region
		 * @private
		 */
		configAriaLive: {
			default: 'polite',
			schema: {type: 'string', enum: ['off', 'polite', 'assertive']}
		},
		/**
		 * An object whose keys reference a specific element within the now-alert-list.
		 * The value of each key is an object that contains an ARIA property (or properties) and corresponding value/s.
		 * These ARIA properties will be set on the specified element.
		 * ARIA properties can be set on the now-alert-list using the key `list`.
		 * Use this property to configure the `aria-live` property on the alert-list, which defaults to 'polite'.
		 * @type {{ list: {'aria-*': string} }}
		 */
		configAria: {
			default: {},
			schema: {
				type: 'object',
				properties: {
					list: configAriaSchema
				},
				additionalProperties: false
			}
		},
		/**
		 * Threshold at which to switch from displaying individual alerts to
		 * displaying them as a stack with a show/hide button and "dismiss all" button.
		 * @type {integer}
		 */
		stackThreshold: {default: 3, schema: {type: 'integer', minimum: 2}},
		/**
		 * Maximum height (as a number of pixels) for each expanded alert. If the
		 * content exceeds the provided height, a scrollbar displays inside the alert.
		 * @type {number}
		 * @uib.description Maximum height (as a number of pixels) for each alert.
		 */
		maxHeight: {schema: {type: 'number', minimum: 50}}
	},
	initialState: {
		expandedAlerts: {},
		userInteractedWithController: false,
		collapsed: false
	},
	view,
	actionHandlers: {
		[COMPONENT_CONNECTED]: ({properties, updateState}) => {
			const {items, stackThreshold} = properties;
			if (items.length >= stackThreshold) {
				updateState({collapsed: true});
			}
		},
		[COMPONENT_PROPERTY_CHANGED]: ({
			state,
			action,
			updateState,
			properties
		}) => {
			const {name, value, previousValue} = action.payload;
			const {stackThreshold} = properties;
			if (name === 'items') {
				const nextLength = value.length;
				const lastLength = previousValue ? previousValue.length : 0;
				if (nextLength !== lastLength) {
					const {collapsed, userInteractedWithController} = state;
					/*
					 * Case 1:
					 * - If alert(s) reduced to 0:
					 * - clear everything so we start fresh when new alerts are added
					 *
					 * Case 2:
					 * - If alert(s) reduced to 1
					 * - and the user didn't actively dismiss the alert (user can't dismiss alerts when list is collapsed):
					 * - expand the list and hide the controller
					 *
					 * Case 3:
					 * - If alert(s) reduced below stackThreshold
					 * - and the list is auto collapsed (aka the user hasn't interacted with the controller):
					 * - expand the list and hide the controller
					 */
					if (
						nextLength === 0 ||
						(nextLength === 1 && collapsed) ||
						(nextLength < stackThreshold &&
							collapsed &&
							!userInteractedWithController)
					) {
						updateState({
							collapsed: false,
							userInteractedWithController: false
						});
					}
					/*
					 * Case 4:
					 * - If alert(s) increased at or above stackThreshold
					 * - and the user hasn't actively expanded then collapsed the list (aka it was auto collapsed and not touched):
					 * - collapse the list and show the controller
					 */
					if (
						nextLength >= stackThreshold &&
						lastLength < stackThreshold &&
						!userInteractedWithController
					) {
						updateState({collapsed: true});
					}
				}
			}
		},
		'NOW_ALERT#ACTION_CLICKED': ({action, dispatch}) => {
			dispatch(({properties}) => {
				const item = properties.items.find(
					(item) => item.id === action.payload.alertId
				);
				return {
					type: 'NOW_ALERT_LIST#ITEM_ACTION_CLICKED',
					payload: {item}
				};
			});
		},
		'NOW_ALERT_LIST#ITEM_ACTION_CLICKED': {
			effect: alertDismissedHandler
		},
		'NOW_ALERT#TEXT_LINK_CLICKED': ({action, dispatch}) => {
			dispatch(({properties}) => {
				const item = properties.items.find(
					(item) => item.id === action.payload.alertId
				);
				return {
					type: 'NOW_ALERT_LIST#ITEM_TEXT_LINK_CLICKED',
					payload: {item}
				};
			});
		},
		'NOW_ALERT_LIST#ITEM_TEXT_LINK_CLICKED': {
			effect: alertDismissedHandler
		},
		'NOW_ALERT#EXPANDED_SET': ({action, updateState}) => {
			const {alertId, value: expanded} = action.payload;
			updateState({
				path: `expandedAlerts.${alertId}`,
				operation: 'set',
				value: expanded
			});
		},
		'PRIVATE#DISMISS_ALL': {
			stopPropagation: true,
			effect: ({dispatch, updateState}) => {
				dispatch(() => {
					return {
						type: 'NOW_ALERT_LIST#ITEMS_SET',
						payload: {value: []}
					};
				});
				updateState({
					collapsed: false,
					userInteractedWithController: false
				});
			}
		}
	},
	styles,
	behaviors: [
		{
			behavior: focusBehavior
		}
	],
	dispatches: {
		/**
		 * Dispatched when an individual alert is dismissed, or when the `dismiss all`
		 * button is used to dismiss all of the alerts in the list. The payload
		 * will contain the array of non-dismissed alerts remaining in the list.
		 * Set the `manage-items` property to override the default behavior and
		 * handle this action manually.
		 * @type {{value: Object[]}}
		 */
		'NOW_ALERT_LIST#ITEMS_SET': {},
		/**
		 * Dispatched when a user clicks on the action button within an
		 * individual alert. Payload will contain the configuration passed in
		 * for that alert item.
		 * @type {{item: Object}}
		 */
		'NOW_ALERT_LIST#ITEM_ACTION_CLICKED': {},
		/**
		 * Dispatched when a user clicks on the text link within an
		 * individual alert. Payload will contain the configuration passed in
		 * for that alert item.
		 * @type {{item: Object}}
		 */
		'NOW_ALERT_LIST#ITEM_TEXT_LINK_CLICKED': {}
	}
});
