import isEmpty from 'lodash/isEmpty';
import {contentIsOverset} from './content-is-overset';
import {getComputedOversetHeight} from './get-computed-overset-height';

const updateOverset = ({updateState, host, action, state}) => {
	const {height, width} = action.payload;
	let nextState = {};

	// This is a setup step on first render only.
	if (!state.contentEl) {
		nextState.contentEl = host.shadowRoot.querySelector('.now-alert-content');
	}

	if (height !== state.prevHeight) {
		const computedOversetHeight = getComputedOversetHeight();
		if (state.oversetHeight !== computedOversetHeight) {
			nextState.oversetHeight = computedOversetHeight;
		}

		nextState.prevHeight = height;
	}

	// In the case where these were updated above.
	const oversetHeight = nextState.oversetHeight || state.oversetHeight;
	const contentEl = nextState.contentEl || state.contentEl;
	const OVERSET_TOLERANCE = 5;
	const oversetHeightRangeMax = oversetHeight + OVERSET_TOLERANCE;
	const oversetHeightRangeMin = oversetHeight - OVERSET_TOLERANCE;

	const contentFits = () =>
		height <= oversetHeightRangeMax && !contentIsOverset(contentEl);
	const contentOverset = () =>
		height >= oversetHeightRangeMin || contentIsOverset(contentEl);

	if (width > state.prevWidth && contentFits()) {
		nextState.isOverset = false;
	} else if (width < state.prevWidth && contentOverset()) {
		nextState.isOverset = true;
	}

	if (width !== state.prevWidth) nextState.prevWidth = width;
	if (!isEmpty(nextState)) {
		updateState(nextState);
	}
};

export const handleResize = (coeffects) =>
	requestAnimationFrame(() => updateOverset(coeffects));
