import {createEnhancedElement} from '@servicenow/library-enhanced-element';
import {
	Fragment,
	dangerouslyCreateElementFromString
} from '@servicenow/ui-renderer-snabbdom';
import styles from './_now-alert.scss';
import '@servicenow/now-button';
import '@servicenow/now-icon';
import '@servicenow/now-text-link';
import {t} from 'sn-translate';
import resizeBehavior from '@servicenow/behavior-resize';
import focusBehavior from '@servicenow/behavior-focus';
import {filterAriaAttributes} from '@servicenow/library-enhanced-utils';
import {handleResize} from './utils/handle-resize';
import {getComputedOversetHeight} from './utils/get-computed-overset-height';
import {configAriaSchema} from '@servicenow/library-schemas';

const normalizeContent = (content) => {
	if (typeof content === 'undefined') {
		return {};
	}
	if (typeof content === 'string') {
		return {type: 'string', value: content};
	}
	return content;
};

const renderIcon = (icon) => {
	return icon ? <now-icon className="now-alert-icon" icon={icon} /> : undefined;
};

const renderActionButton = (action = {}, status, configAria, onClick) => {
	const {type} = action;

	if (type === 'open') {
		return (
			<now-button
				className="now-alert-button"
				variant="tertiary"
				label={action.label ? action.label : t('Open')}
				{...filterAriaAttributes(configAria, 'action')}
				config-aria={{button: {'aria-label': t('Open and dismiss alert')}}}
				on-click={onClick}
			/>
		);
	}

	if (type === 'acknowledge') {
		return (
			<now-button
				className="now-alert-button"
				variant="tertiary"
				label={action.label ? action.label : t('OK')}
				{...filterAriaAttributes(configAria, 'action')}
				config-aria={{button: {'aria-label': t('Dismiss alert')}}}
				on-click={onClick}
			/>
		);
	}

	if (type === 'dismiss') {
		return (
			<now-button-iconic
				class={{
					'now-alert-button': true,
					'is-iconic': true,
					['-' + status]: status
				}}
				variant="tertiary"
				config-aria={{button: {'aria-label': t('Dismiss alert')}}}
				{...filterAriaAttributes(configAria, 'action')}
				on-click={onClick}
				icon="close-outline"
				bare
			/>
		);
	}

	return '';
};

const renderExpandButton = (isOverset, contentIsReady, expanded, onClick) => {
	// Check for prevHeight so that the content doesn't expand and shrink
	// suddenly on first render due to this element
	return isOverset && contentIsReady ? (
		<now-button-bare
			className="now-m-block-start--xxs"
			on-click={onClick}
			variant="secondary"
			hide-padding
			high-contrast
			label={expanded ? t('Show less') : t('Show more')}
			icon-end={expanded ? 'caret-up-fill' : 'caret-down-fill'}
			config-aria={{
				'aria-hidden': 'true'
			}}
		/>
	) : (
		undefined
	);
};

const renderContent = (content) => {
	if (content.type === 'string') {
		return <span>{content.value}</span>;
	}
	if (content.type === 'jsx') {
		return content.value;
	}
	if (content.type === 'html') {
		return dangerouslyCreateElementFromString(content.value);
	}
};

const view = (state, {dispatch}) => {
	const {prevHeight: contentIsReady, isOverset} = state;
	const {
		status,
		icon = state.properties.iconName,
		action,
		header,
		content: rawContent,
		textLinkProps,
		expanded,
		showShadow,
		configAria,
		maxHeight
	} = state.properties;

	const content = normalizeContent(rawContent);

	// `shouldExpand` will only use `expanded` after COMPONENT_RENDERED,
	// as that's when we've retrieved width and height values from resize behavior.
	// If we don't do this, all alerts will initially render  the'show more' or
	// 'show less' button and then immediately unrender it if not overset.
	const shouldExpand = contentIsReady ? expanded : false;
	const onTextLinkClick = () =>
		dispatch(() => {
			return {
				type: 'NOW_ALERT#TEXT_LINK_CLICKED',
				payload: {}
			};
		});

	return (
		<div
			role="status"
			class={{
				'now-alert': true,
				['-' + status]: status,
				'show-shadow': showShadow
			}}>
			{renderIcon(icon)}
			<div className="now-alert-container">
				<div className="now-line-height-crop">
					<div
						class={{
							'now-alert-content': true,
							'is-expanded': shouldExpand
						}}
						style={maxHeight ? {'max-height': `${maxHeight}px`} : undefined}
						// eslint-disable-next-line jsx-a11y/no-noninteractive-tabindex
						tabindex={expanded && maxHeight ? 0 : undefined}>
						{header ? (
							<Fragment>
								<strong>{header}</strong>{' '}
								{/* This "natural" space separates `header` from `content`. Don't delete. */}
							</Fragment>
						) : (
							undefined
						)}
						{renderContent(content)}
						{textLinkProps && textLinkProps.href && textLinkProps.label ? (
							<Fragment>
								{' '}
								{/* This "natural" space separates `content` from `linkText`. Don't delete. */}
								<now-text-link
									href={textLinkProps.href}
									label={textLinkProps.label}
									variant="secondary"
									underlined
									opens-window
									on-click={onTextLinkClick}
								/>
							</Fragment>
						) : (
							undefined
						)}
					</div>
				</div>
				{renderExpandButton(isOverset, contentIsReady, expanded, () => {
					dispatch(({properties}) => ({
						type: 'NOW_ALERT#EXPANDED_SET',
						payload: {value: !properties.expanded}
					}));
				})}
			</div>
			{renderActionButton(action, status, configAria, () =>
				dispatch(() => {
					return {
						type: 'NOW_ALERT#ACTION_CLICKED',
						payload: {action}
					};
				})
			)}
		</div>
	);
};

/**
 * A notification that can be optionally dismissed.
 *
 * ```jsx
 * <now-alert status="warning" icon="exclamation-triangle-fill" header="Warning:" content="Something's not right." text-link-props={{label: 'More info', href: '#'}}></now-alert>
 * ```
 *
 * @seismicElement now-alert
 * @summary Provides a message to the user as helpful information or warning.
 * @uib.label Alert
 * @uib.icon alert
 * @uib.description A notification that can be optionally dismissed
 * @uib.category primitives
 * @uib.associatedTypes global.core
 * @uib.properties status, content, icon, header, textLinkProps, action, expanded, maxHeight
 * @uib.actions NOW_ALERT#EXPANDED_SET, NOW_ALERT#ACTION_CLICKED, NOW_ALERT#TEXT_LINK_CLICKED
 */
createEnhancedElement('now-alert', {
	properties: {
		/**
		 * Sets the alert color to indicate priority.
		 * @type {('critical'|'high'|'moderate'|'warning'|'info'|'positive'|'low')}
		 * @uib.label Style
		 * @uib.description Sets the alert color to indicate priority
		 */
		status: {
			default: 'info',
			schema: {
				type: 'string',
				enum: [
					'critical',
					'high',
					'moderate',
					'warning',
					'info',
					'positive',
					'low'
				]
			}
		},
		/** @private */
		iconName: {},
		/**
		 * Optional icon displayed at the start of the alert.
		 * See the `now-icon` component documentation for valid inputs.
		 * @type {string}
		 * @uib.description Optional icon displayed at the start of the alert
		 * @uib.defaultValue circle-info-outline
		 * @uib.fieldType icon
		 */
		icon: {schema: {type: 'string'}},
		/**
		 * Optional title for the alert.
		 * @type {string}
		 * @uib.description Optional title for the alert
		 * @uib.defaultValue Heads up
		 * @uib.translatable true
		 */
		header: {schema: {type: 'string'}},
		/**
		 * Optional message for the alert. Can be a string or an object with the
		 * keys `type` and `value`.
		 * @type {(string|Object)}
		 * @uib.label Message
		 * @uib.description Optional message for the alert.
		 * @uib.defaultValue This needs your attention
		 * @uib.fieldType string
		 */
		content: {
			schema: {
				oneOf: [
					{type: 'string', translatable: true},
					{
						type: 'object',
						properties: {
							type: {type: 'string', enum: ['string', 'html', 'jsx']},
							value: {
								oneOf: [{type: 'string', translatable: true}, {type: 'object'}]
							}
						}
					}
				]
			}
		},
		/**
		 * Object that defines an optional link in the alert. The link
		 * opens in a new window when the user clicks it.
		 *
		 * The action `NOW_ALERT#TEXT_LINK_CLICKED` is dispatched when the user
		 * clicks on the text link. The parent component or application must handle
		 * this action and dismiss the alert.
		 *
		 * The keys of the object are properties for `now-text-link`. See
		 * `now-text-link` for more documentation on these properties:
		 * - `label`: String. Text displayed as a link inside the alert. Required.
		 * - `href`: String. Link URL opened when the user clicks the
		 *   link text. Required.
		 * @type {{label: string, href: string}}
		 * @uib.label Link
		 * @uib.description Adds a link that dispatches `NOW_ALERT#TEXT_LINK_CLICKED` when clicked
		 * @uib.defaultValue {"label": "Link label", "href": "dummyHref"}
		 */
		textLinkProps: {
			schema: {
				type: 'object',
				properties: {
					label: {type: 'string', translatable: true},
					href: {type: 'string'}
				}
			}
		},
		/**
		 * Object that defines an optional action to take in the alert message. If
		 * this property is not set, the user can not dismiss the alert.
		 *
		 * The action `NOW_ALERT#ACTION_CLICKED` is dispatched when the user clicks
		 * on the action. Its payload contains this `action` object. The parent
		 * component or application must handle this action and dismiss the alert
		 * and/or open a new window depending on the action type.
		 *
		 * Action objects are configured with the following keys:
		 * - `type`: String. Sets the type of action, see below. Required.
		 * - `href`: String. Use with type "open" to set the URL to
		 *   open when the user clicks the button. Required.
		 * - `label`: String. Use to override the default button text for actions
		 *   of type "open" or "acknowledge". If the label is overriden, update the
		 *   button's corresponding aria-label through the configAria property.
		 *
		 * Set the key `type` to pick an action:
		 * - "dismiss": Displays an (X) button.
		 * - "acknowledge": Displays an (OK) button by default, or a unique button name using the `label` property.
		 * - "open": Displays an (Open) button by default, or a unique button name using the `label` property.
		 * @type {{type: ("dismiss"|"acknowledge"|"open"), href: (string|undefined), label: (string|undefined)}}
		 * @uib.description Adds an action to the alert, preventing the user from dismissing it
		 * @uib.defaultValue {"type": "acknowledge", "label": "Custom ok"}
		 */
		action: {
			schema: {
				type: 'object',
				properties: {
					type: {type: 'string', enum: ['dismiss', 'acknowledge', 'open']},
					href: {type: 'string'},
					label: {type: 'string', translatable: true}
				}
			}
		},
		/**
		 * An object whose keys reference a specific HTML element within the
		 * now-alert. The value of each key is an object that contains an ARIA
		 * property (or properties) and corresponding value/s. These ARIA
		 * properties are set on the specified element.
		 * The 'action' key corresponds to the action button in
		 * now-alert. See https://www.w3.org/TR/wai-aria-1.1/#button for
		 * properties and accepted values.
		 * @type {{ 'aria-*': string }}
		 */
		configAria: {
			schema: {
				oneOf: [
					{
						type: 'object',
						properties: {
							action: configAriaSchema
						},
						additionalProperties: false
					},
					configAriaSchema
				]
			}
		},
		/**
		 * Specifies if the alert is expanded to show content beyond the
		 * default alert size. Use `manage-expanded` to override the default behavior
		 * and handle the `NOW_ALERT#EXPANDED_SET` action manually.
		 * @type {boolean}
		 * @uib.description Expands the alert to show content beyond the default alert size
		 */
		expanded: {default: false, manageable: true, schema: {type: 'boolean'}},
		/** @private */
		showShadow: {default: false, schema: {type: 'boolean'}},
		/**
		 * Maximum height in pixels for the content area of an
		 * expanded alert. If the content exceeds the provided height, a
		 * scrollbar displays inside the alert.
		 * @type {number}
		 * @uib.description Maximum height in pixels for the content area of the alert.
		 */
		maxHeight: {schema: {type: 'number', minimum: 40}}
	},
	dispatches: {
		/**
		 * Dispatched when an alert whose content overflows the default container
		 * size is expanded or collapsed to show/hide the additional content.
		 * Set the `manage-expanded` property to override the default behavior and
		 * handle this action manually.
		 * @type {{value: boolean}}
		 * @uib.description Dispatched when an alert whose content overflows the default container size is expanded or collapsed to show/hide the additional content
		 */
		'NOW_ALERT#EXPANDED_SET': {
			schema: {
				type: 'object',
				properties: {
					value: {type: 'boolean'}
				}
			}
		},
		/**
		 * Dispatched when the user clicks on the action. Its payload contains
		 * the `action` object passed in to the alert's configuration. The parent
		 * component or application must handle this action and dismiss the alert
		 * and/or open a new window, depending on the action type.
		 * @type {{action: Object}}
		 * @uib.description Dispatched when the user clicks on the action
		 */
		'NOW_ALERT#ACTION_CLICKED': {
			schema: {
				type: 'object',
				properties: {
					action: {type: 'object'}
				}
			}
		},
		/**
		 * Dispatched when the user clicks on the text link. The parent component
		 * or application must handle this action and dismiss the alert.
		 * @type {{}}
		 * @uib.description Dispatched when the user clicks on the text link
		 */
		'NOW_ALERT#TEXT_LINK_CLICKED': {
			schema: {
				type: 'object'
			}
		}
	},
	initialState: {
		contentEl: null,
		prevWidth: 0,
		prevHeight: 0,
		oversetHeight: getComputedOversetHeight(),
		isOverset: true
	},
	actionHandlers: {
		'NOW_RESIZE#ELEMENT_RESIZED': handleResize
	},
	behaviors: [
		{behavior: resizeBehavior, options: {timer: {type: 'throttle', wait: 50}}},
		{behavior: focusBehavior}
	],
	styles,
	view
});
